@charset "UTF-8";
@import "https://fonts.googleapis.com/css?family=Noto+Sans";
.bodyb {
    margin: 0;
    padding: 0;
    height: 100vh;
    background: url(uni2.jpg);
    background-size: cover;
    font-family: sans-serif;
}

.loginBox {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    height: 420px;
    padding: 80px 40px;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.5);
}

.user {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: -50px;
    left: 130px;
}
.Modal a {
    display: block;
    color: rgba(255, 255, 255, 0.7);
    font-size: 12px;
    text-align: center;
    text-decoration: none;
    margin: 4px 0 10px 0;
    margin: 10px;
}
h2 {
    margin: 0;
    padding: 0 0 20px;
    color: #fff;
    text-align: center;
}

.loginBox p {
    margin: 0;
    padding: 0;
    font-weight: bold;
    color: #fff;
}

.loginBox input {
    width: 100%;
    margin-bottom: 20px;
}
.loginBox input[type="text"],
.loginBox input[type="password"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
    color: white;
    font-size: 16px;
    font-weight: bold;
}
::placeholder {
    color: rgba(255, 255, 255, 0.5);
}
.loginBox input[type="submit"] {
    border: none;
    outline: none;
    height: 40px;
    color: #fff;
    font-size: 16px;
    background: #8b0000;
    cursor: pointer;
    border-radius: 20px;
}

.loginBox input[type="submit"]:hover {
    background: #b22222;
    color: #fff;
}
.olvideContrase単a {
    border: none;
    outline: none;
    height: 40px;
    color: #fff;
    font-size: 16px;
    background: #8b0000;
    cursor: pointer;
    border-radius: 20px;
    width: 14vw;
}

.olvideContrase単a:hover {
    background: #b22222;
    color: #fff;
}

.arrow {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 10px;
    left: 10px;
    cursor: pointer;
}
