.list {
  bottom: -0.5vh;
  display: flex;
  flex-direction: column;
  left: -1.8vw;
  text-align: center;
  background: #ececec;
  border: 0px solid;
  height: 25%;
  border-radius: 7px 0px 0px 7px;
  overflow: hidden;
  overflow-y: scroll;
}

.listele {
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: #b8b8b8;
  background: rgb(216, 216, 221);
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
}
.dropdown {
  position: relative;
  bottom: -5vh;
  margin-left: 1vw;
}
.bod {
  position: relative;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.texto {
  position: relative;
  padding-left: 2vw;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
}
.but {
  position: relative;
  height: 5vh;
  font-family: "Courier New", Courier, monospace;
  font-size: 13px;
  background-color: rgb(184, 184, 184);
  margin-top: 0vh;
  margin-left: 10vw;
  margin-right: 10vw;
  border-radius: 10px;
  transition: 0.5s;
  top: 12vh;
  font-weight: bold;
}

.but:hover {
  background-color: rgb(89, 206, 89);
}

.inputtext {
  position: relative;
  width: 86%;
  margin-left: 1.8vw;
  margin-right: 1.8vw;
  border-radius: 7px 0px 0px 7px;
  height: 10vh;
  margin-top: 0vh;
  font-family: "Courier New", Courier, monospace;
  resize: none;
  overflow: hidden;
  overflow-y: scroll;
  background: #ececec;
  top: 9vh;
  font-weight: bold;
}
::placeholder {
  color: black;
  font-family: "Courier New", Courier, monospace;
  opacity: 0.5;
  font-weight: bold;
}

.texti {
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
}

.div1 {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 3vh;
  width: 100%;
  margin-top: 2vh;
}

.div2 {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.div3 {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 0vh;
  top: 5vh;
}

.allCaseContainer {
  position: relative;
  background-color: rgb(255, 255, 255);
  box-shadow: -10px 15px 30px black;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 30%;
  overflow: auto;
}
.optionsCase {
  width: 33.33%;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  height: 6vh;
}

.optionsCase:focus {
  outline: 0;
}
.optionsCase:active {
  outline: 0;
}
.caseNav {
  display: flex;
}

.openCaseContainer {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  list-style: none;
  overflow: auto;
}
.activeCaseLoop {
  width: 100%;
  display: flex;
  flex-direction: column;
  border-bottom: 3px solid black;
  background: rgb(216, 216, 221);
}

.rowCase {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.requests {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.objects {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
}
.listApoyo {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.requestsContainer {
  width: 100%;
  height: 100%;
  display: flex;
}

.buttonObj {
  border-radius: 30px;
  background-color: red;
  font-weight: bold;
}
.buttonObj:focus {
  outline: 0;
}

.close-icon {
  display: block;
  box-sizing: border-box;
  width: 20px;
  height: 40px;
  border-width: 3px;
  border-style: solid;
  border-color: red;
  border-radius: 100%;
  background: -webkit-linear-gradient(
      -45deg,
      transparent 0%,
      transparent 46%,
      white 46%,
      white 56%,
      transparent 56%,
      transparent 100%
    ),
    -webkit-linear-gradient(45deg, transparent 0%, transparent 46%, white 46%, white
          56%, transparent 56%, transparent 100%);
  background-color: red;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
  margin-left: 92%;
  margin-top: 1%;
  cursor: pointer;
}
@media (max-width: 909px) {
  .allCaseContainer {
    width: 40%;
  }
}

@media (max-width: 650px) {
  .allCaseContainer {
    width: 80%;
  }
}
