.tableStats {
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  border: 1px solid black;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  border: 1px solid black;
  text-align: center;
}

tr:nth-child(even) {
  background-color: #dddddd;
}
